// ajax.js  – vanilla‑JS version
// ----------------------------------------------

const BASE = "https://indoorlensmaps.azurewebsites.net/api/";
const FUNCTION_KEY =
  "Gh8s0sQm4Uy1U3bW-6MQ2M3U3PXxpPvEngTNcYmLvBPJAzFuc1df1w==";

/* ------------------------------------------------------------------ */
/* helper: fetch JSON and throw on non‑OK                              */
async function getJson(url) {
  const resp = await fetch(url, { mode: "cors" });
  if (!resp.ok) throw new Error(`${resp.status} ${resp.statusText} – ${url}`);
  return resp.json();
}

/* ------------------------------------------------------------------ */
/* load map, layout, path in parallel                                  */
export async function loadInitialData({
  onMap,
  onLayout,
  onPath
}) {
  const mapId   = window.IndoorLens?.cfg?.mapid ?? "";
  console.log("Loading map data for mapId:", mapId);

  const loadingOverlay = document.getElementById("loadingOverlay");
  const deadOverlay = document.getElementById("deadOverlay");
  loadingOverlay?.classList.remove("d-none");
  deadOverlay?.classList.add("d-none");

  try {
    const mapUrl    = `${BASE}MapHandler?filename=${mapId}/map.txt&code=${FUNCTION_KEY}`;
    const layoutUrl = `${BASE}LayoutHandler?filename=${mapId}/layout.txt&code=${FUNCTION_KEY}`;
    const pathUrl   = `${BASE}PathHandler?filename=${mapId}/path.txt&code=${FUNCTION_KEY}`;

    const [mapJson, layoutJson, pathJson] = await Promise.all([
      getJson(mapUrl),
      getJson(layoutUrl),
      getJson(pathUrl)
    ]);

    onMap?.(mapJson);
    onLayout?.(layoutJson);
    onPath?.(pathJson);
  } catch (err) {
    console.error("loadInitialData error:", err);
    deadOverlay?.classList.remove("d-none");
  } finally {
    loadingOverlay?.classList.add("d-none");
  }
}

/* ------------------------------------------------------------------ */
/* fetch a path‑finding result                                         */
export async function requestPathFinding({
  fromId,
  toId,
  WCHR = true,
  onResult
}) {
  console.log("fromId:", fromId);
  console.log("toId:", toId);
  console.log("WCHR:", WCHR);
 
  const mapId   = window.IndoorLens?.cfg?.mapid ?? "";
  const loadingOverlay = document.getElementById("loadingOverlay");
  const deadOverlay = document.getElementById("deadOverlay");

  loadingOverlay?.classList.remove("hidden");
  deadOverlay?.classList.add("hidden");
  console.log("Requesting path from", fromId, "to", toId);

  try {
    const url =
      `${BASE}PathFindingHandler` +
      `?code=${FUNCTION_KEY}` +
      `&mapid=${encodeURIComponent(mapId)}` +
      `&from=${encodeURIComponent(fromId)}` +
      `&to=${encodeURIComponent(toId)}` +
      `&WCHR=${WCHR}`;

    const json = await getJson(url);
    onResult?.(json);
  } catch (err) {
    console.error("PathFinding fetch error:", err);
    deadOverlay?.classList.remove("hidden");
  } finally {
    loadingOverlay?.classList.add("hidden");
  }
}