import { loadInitialData } from "./cloud.js";
import { initInputs } from "./inputs.js";
import { setMapData, setLayoutData, setPathData, initCanvas } from "./preview.js";


export class IndoorLensWidget {
  constructor(root, opts) {
    this.root = root;          // <div> host element
    this.opts = opts;
    injectIndoorLensCSS();       // inject CSS once
    // build all DOM elements inside the host and get references
    const refs = buildIndoorLensDOM(this.root, { width: this.opts.width, height: this.opts.height });

    // store element refs
    this.canvas = refs.canvas;
    this.loadingOverlay = refs.loadingOverlay;
    this.deadOverlay = refs.deadOverlay;
    this.fromInput = refs.fromInput;
    this.toInput = refs.toInput;
    this.wchrBtn = refs.wchrBtn;
    this.walkBtn = refs.walkBtn;
    this.groupLabel = refs.groupLabel;

    // initialize canvas rendering
    initCanvas(this.canvas);
    // attach event listeners on inputs and buttons
    initInputs(this.root);

    // load data + show spinner
    this.initLogic();
  }

  /* attach listeners + load map / layout / path */
  initLogic() {
    // set current map and show spinner
    //  window.IndoorLens.mapid = this.opts.mapid;
    this.loadingOverlay?.classList.remove("hidden");

    // load initial map/layout/path data
    loadInitialData({
      onMap: json => setMapData(json),
      onLayout: json => setLayoutData(json),
      onPath: json => setPathData(json)
    })
      .finally(() => {
        // hide spinner when done
        this.loadingOverlay?.classList.add("hidden");
      });
  }
}


