

// /wwwroot/js/indoorlens.js  (the file you called “factory”)

/* 1. import the functional modules  */
import "./models.js";        // defines Vec3, enums, data classes
import "./inputs.js";        // attaches input listeners, exposes events
import "./cloud.js";          // data loading helpers
import "./control.js";       // UI buttons, group +/- logic


/* ---------- public init: builds a widget on the given host ---------------- */
export function init(options = {}) {
  const cfg = Object.assign(
    { },
    options
  );

  // expose basic config on window for other modules that rely on it
  window.IndoorLens = window.IndoorLens || {};
  window.IndoorLens.cfg = { target: cfg.target,mapid: cfg.mapid, apiKey: cfg.apiKey,unit: cfg.unit };
  // expose mapid and apiKey directly for legacy access

  window.IndoorLens.mapid = cfg.mapid;
  window.IndoorLens.apiKey = cfg.apiKey;
  window.IndoorLens.unit = cfg.unit;

  const host =
    typeof cfg.target === "string"
      ? document.querySelector(cfg.target)
      : cfg.target;

  if (!host) {
    throw new Error("IndoorLens: target element not found");
  }

  import("./indoorlenswidjet.js").then(({ IndoorLensWidget }) => {
    // do this:

    const widget= new IndoorLensWidget(host, cfg);
 
  //  window.IndoorLens = window.IndoorLens || {};
   window.IndoorLensWidget = widget;
  });
}
/* make the API available to non‑module scripts*/
if (typeof window !== "undefined") {
  window.IndoorLens = window.IndoorLens || {};
  window.IndoorLens.init = init;   // ← attaches the function
} 