import { LocationTypeName } from './models.js'; // 
import { draw, setPathFindingResultData, mapData, layoutData } from "./preview.js";
import { requestPathFinding } from "./cloud.js";
import { getGroupIndex } from "./preview.js";
//import { ,setPathData,setLayoutData,setMapData} from "./preview.js";

export let fromId = null;
export let toId = null;
export let groupIndex = 0;
let wchr = false;


export function initInputs() {

    /* ---------- group ± buttons ---------- */
    const btnPlus = document.getElementById('btnPlus');
    const btnMinus = document.getElementById('btnMinus');
    const groupLbl = document.getElementById('groupLabel');

    const wchrBtn = document.getElementById('wchr');
    const walkBtn = document.getElementById('walk');



    function updateGroupLabel() {
        groupLbl.textContent = groupIndex;
    }
    updateGroupLabel();

    btnPlus.addEventListener('click', () => {
        if (!layoutData) return;
        groupIndex = Math.min(groupIndex + 1, layoutData.groups.length - 1);
        updateGroupLabel();
        draw();
    });

    btnMinus.addEventListener('click', () => {
        if (!layoutData) return;
        groupIndex = Math.max(groupIndex - 1, 0);
        updateGroupLabel();
        draw();
    });

    walkBtn.addEventListener('click', () => {
        wchr = true;
        wchrBtn.style.display = 'flex';
        walkBtn.style.display = 'none';
        if (fromId != null && toId != null) {
            requestPathFinding({
                fromId,                  // same‑name shorthand
                toId,
                WCHR: wchr,             // optional; defaults to true
                onResult: result => {    // matches the onResult param
                    setPathFindingResultData(result);
                }
            });
        }
    });

    wchrBtn.addEventListener('click', () => {
        wchr = false;
        wchrBtn.style.display = 'none';
        walkBtn.style.display = 'flex';
        if (fromId != null && toId != null) {
            requestPathFinding({
                fromId,                  // same‑name shorthand
                toId,
                WCHR: wchr,             // optional; defaults to true
                onResult: result => {    // matches the onResult param
                    setPathFindingResultData(result);
                }
            });
        }
    });




    const inputFrom = document.getElementById("from");
    const inputTo = document.getElementById("to");
    const resultsListFrom = document.getElementById("fromResultsList");
    const resultsListTo = document.getElementById("toResultsList");
    const btnFromClear = document.getElementById("btnFromClear");
    const btnToClear = document.getElementById("btnToClear");

    // toggle on every keystroke / paste / delete
    inputFrom.addEventListener("input", () => {
        btnFromClear.disabled = inputFrom.value.trim() === "";
    });

    // optional: clicking the X clears the field
    btnFromClear.addEventListener("click", () => {
        inputFrom.value = "";
        btnFromClear.disabled = true;
        inputFrom.focus();           // put the cursor back in the box

        fromId = null;
        setPathFindingResultData(null);
        draw();
    });


    // toggle on every keystroke / paste / delete
    inputTo.addEventListener("input", () => {
        btnToClear.disabled = inputTo.value.trim() === "";
    });

    // optional: clicking the X clears the field
    btnToClear.addEventListener("click", () => {
        inputTo.value = "";
        btnToClear.disabled = true;
        inputFrom.focus();           // put the cursor back in the box

        toId = null;
        setPathFindingResultData(null);
        draw();
    });


    /**
     * Normalize string for case‐ and accent‐insensitive search
     */
    function normalizeForSearch(str) {
        return str
            .toLowerCase()
            .normalize('NFD')
            .replace(/[\u0300-\u036f]/g, '');
    }

    /**
     * Search with priority: name → tags → type
     */
    function searchLocationsWithPriority(locations, query) {
        const q = normalizeForSearch(query.trim());
        if (!q) return [];

        const nameMatches = [];
        const tagMatches = [];
        const typeMatches = [];

        for (const loc of locations) {
            const nameStr = normalizeForSearch(loc.name ?? '');
            const tagsStr = normalizeForSearch(loc.tags ?? '');
            const typeStr = normalizeForSearch(LocationTypeName[loc.locationType] || '');

            if (nameStr.includes(q)) {
                nameMatches.push(loc);
            } else if (tagsStr.includes(q)) {
                tagMatches.push(loc);
            } else if (LocationTypeName[loc.locationType] != 'Custom' && typeStr.includes(q)) {
                typeMatches.push(loc);
            }
        }

        // Optional: within each bucket you can sort by earliest position:
        const sortByIndex = (arr, fieldExtractor) =>
            arr.sort((a, b) => {
                const hayA = normalizeForSearch(fieldExtractor(a));
                const hayB = normalizeForSearch(fieldExtractor(b));
                return hayA.indexOf(q) - hayB.indexOf(q);
            });

        sortByIndex(nameMatches, loc => loc.name ?? '');
        sortByIndex(tagMatches, loc => loc.tags ?? '');
        sortByIndex(typeMatches, loc => LocationTypeName[loc.locationType] ?? '');

        // Concatenate in priority order:
        return [...nameMatches, ...tagMatches, ...typeMatches];
    }

    // usage:


    // reusable autocomplete setup
    function setupAutocomplete(inputEl, resultsEl) {
        inputEl.addEventListener("input", () => {

            const q = inputEl.value.trim().toLowerCase();
            if (!q) {
                resultsEl.innerHTML = "";
                return;
            }
            const matches = searchLocationsWithPriority(mapData.locations, q);


            resultsEl.innerHTML = matches
                .map(loc => {
                    const floor = getGroupIndex(loc.groupId);
                    const displayName = loc.name
                        ? loc.name
                        : LocationTypeName[loc.locationType];
                    return `<li
            class="list-group-item list-group-item-action p-1"
            data-id="${loc.id}"
          >
            ${displayName} — floor ${floor}
          </li>`;
                })
                .join("");
        });

        // delegate click handling so you can pick an entry
        resultsEl.addEventListener("click", e => {
            const li = e.target.closest("li");
            if (!li) return;
            const selectedId = li.dataset.id;
            const selectedText = li.textContent.trim();

            // set the input value to the chosen text
            inputEl.value = selectedText;
            let whichField = inputEl.id;
            // clear the dropdown
            resultsEl.innerHTML = "";

            // call your handler: e.g. store the chosen ID somewhere


            if (whichField == 'from') fromId = selectedId;
            else if (whichField == 'to') toId = selectedId;
            draw();

            if (fromId != null && toId != null) {
                requestPathFinding({
                    fromId,                  // same‑name shorthand
                    toId,
                    WCHR: wchr,             // optional; defaults to true
                    onResult: result => {    // matches the onResult param
                        setPathFindingResultData(result);
                    }
                });

            }
        });
    }

    // wire up both fields
    setupAutocomplete(inputFrom, resultsListFrom);
    setupAutocomplete(inputTo, resultsListTo);
}   // end initInputs

