import { draw } from "./preview.js";
export let scale = 5; // current zoom
export let rotation = 0;          // radians
export let pan = { x: 0, y: 0 }; // current pan offset (in screen px)

export function attachControls(canvas) {

    /* ---------- unified wheel handler (smooth rotation, pan, zoom) ---------- */
    let rotating = false;                // true while in rotation inertia
    let rotateVelocity = 0;              // keep last angular speed
    let lastWheelTime = 0;
 
    canvas.addEventListener("wheel", e => {
        const factor = (e.deltaMode === 1 ? 15 : 1);   // normalize units
        const now = performance.now();

        const rect = canvas.getBoundingClientRect();
        // calculate pointer offset in CSS pixels relative to canvas center
        const mx = e.clientX - rect.left - (rect.width / 2) - pan.x;
        const my = e.clientY - rect.top - (rect.height / 2) - pan.y;

        /* -- if inertia is active but Alt is no longer held and the user is
             performing a predominantly vertical wheel (deltaY > deltaX),
             stop the rotation so a new zoom gesture can begin -- */
        if (rotating && !e.altKey && Math.abs(e.deltaY) > Math.abs(e.deltaX)) {
            rotating = false;   // cancel inertia
            rotateVelocity = 0;
        }

        /* ---------- ROTATE (Alt or inertia) ---------- */
        if (e.altKey) rotating = true;                 // start / keep rotation

        if (rotating) {
            e.preventDefault();

            // Some drivers emit rotation as deltaX, others as deltaX + deltaY.
            const dTheta = (e.deltaX || -e.deltaY) * 0.002 * factor;
            rotation += dTheta;
            rotateVelocity = dTheta;
            lastWheelTime = now;

            // keep pivot fixed
            const r = rot(mx, my, dTheta);
            pan.x += mx - r.x;
            pan.y += my - r.y;

            draw();
            return;
        }

        /* ---------- PAN (Shift) ---------- */
        if (e.shiftKey) {
            e.preventDefault();
            pan.x -= e.deltaX * factor;
            pan.y -= e.deltaY * factor;
            draw();
            return;
        }

        /* ---------- ZOOM (default) ------- */
        e.preventDefault();
        const z = Math.exp(-e.deltaY * 0.0015 * factor);
        scale *= z;
        pan.x -= mx * (z - 1);
        pan.y -= my * (z - 1);
        draw();
    }, { passive: false });
 

    let lastTouches = [];
    canvas.addEventListener("touchstart", e => {
        e.preventDefault();
        lastTouches = [...e.touches];
    }, { passive: false });

    canvas.addEventListener("touchmove", e => {
        e.preventDefault();
        const [t0, t1] = lastTouches;
        const [u0, u1] = e.touches;

        if (e.touches.length === 1) {
            // pan
            const dx = u0.clientX - t0.clientX;
            const dy = u0.clientY - t0.clientY;
            pan.x += dx;
            pan.y += dy;
        }
        else if (e.touches.length === 2) {
            // midpoint before and after for pivot
            const prevMidX = (t0.clientX + t1.clientX) / 2;
            const prevMidY = (t0.clientY + t1.clientY) / 2;
            const currMidX = (u0.clientX + u1.clientX) / 2;
            const currMidY = (u0.clientY + u1.clientY) / 2;

            // PINCH-ZOOM
            const prevDist = Math.hypot(t1.clientX - t0.clientX, t1.clientY - t0.clientY);
            const currDist = Math.hypot(u1.clientX - u0.clientX, u1.clientY - u0.clientY);
            const z = currDist / prevDist;
            scale *= z;

            // ROTATE around midpoint
            const prevAng = Math.atan2(t1.clientY - t0.clientY, t1.clientX - t0.clientX);
            const currAng = Math.atan2(u1.clientY - u0.clientY, u1.clientX - u0.clientX);
            const dAng = currAng - prevAng;
            rotation += dAng;

            // adjust pan so the midpoint stays fixed
            pan.x += (currMidX - prevMidX);
            pan.y += (currMidY - prevMidY);
        }

        lastTouches = [...e.touches];
        draw();
    }, { passive: false });

    /* ----- inertia ticker: let rotation glide to a stop ----- */
    function tick() {
        requestAnimationFrame(tick);

        if (!rotating) return;

        const now = performance.now();
        const dt = (now - lastWheelTime) / 1000;   // seconds since last wheel

        // basic friction model
        rotateVelocity *= 0.94 ** (dt * 60);
        if (Math.abs(rotateVelocity) < 0.0001) {
            rotating = false;
            return;
        }

        rotation += rotateVelocity;
        draw();
    }
    tick();
}

function rot(vx, vy, angle) {
    const cos = Math.cos(angle), sin = Math.sin(angle);
    return { x: vx * cos - vy * sin, y: vx * sin + vy * cos };
}