export class LocationModel {
  constructor(src = {}) {
    /* raw JSON has branches:[...] at the root */
    this.branchKey = src.branchKey ?? 0;
    this.distance = src.distance ?? 0;

  }

}


export class MapLocationModel {
  constructor(src = {}) {
    /* raw JSON has branches:[...] at the root */
    this.id = src.id ?? '';
    this.name = src.name ?? '';
    this.tags = src.tags ?? '';
    this.groupId = src.groupId ?? '';
    this.center = new Vec3(src.center);
    this.locationType = Number(src.locationType) ?? LocationType.Custom;
    this.locationDisplayType = Number(src.locationDisplayType) ?? LocationDisplayType.Custom;
    this.locations = (src.locations ?? []).map(b => new LocationModel(b));
    this.surfaceBranchKeys = [...(src.surfaceBranchKeys ?? [])];
  }
}


export class MapData {
  constructor(src = {}) {
    /* raw JSON has branches:[...] at the root */
    this.id = src.id ?? '';
    this.name = src.name ?? '';
    this.tags = src.tags ?? '';
    this.locations = (src.locations ?? []).map(b => new MapLocationModel(b));
  }
}

export class Vec3 {
  /**
   * @param {{ X?: number, Y?: number, Z?: number, x?: number, y?: number, z?: number }} obj
   */
  constructor(obj = {}) {
    // pick whichever casing your JSON has
    this.x = obj.x ?? obj.X ?? 0;
    this.y = obj.y ?? obj.Y ?? 0;
    this.z = obj.z ?? obj.Z ?? 0;
  }
}
